import React from 'react';
import Buttons from '../components/Buttons';
import ProjectComponent from '../components/ProjectComponent';
import SwipeablePage from '../components/SwipeablePage';

const Project3 = () => {

    const { currentPage, setCurrentPage, handleSwipe } = SwipeablePage();

    const backColor = 'gold';
    const textColor = 'mediumslateblue';
    const shadowColor = '0 0 20px rgb(217, 66, 217)';
    const blobColor = 'blob-purple';

    return (
        <div {...handleSwipe} className="project">
            <ProjectComponent projectNumber={2}            
              backColor={backColor}
              textColor={textColor}
              shadowColor={shadowColor}
              blobColor={blobColor} />
            <Buttons up={"/projet-2"} down={"/projet-4"} linkUp={"Projet 2"} linkDown={'Projet 4'} />
        </div>
    );
};

export default Project3;