import React, { useEffect, useState } from 'react';
import { projectsData } from '../data/projectsData';
import { motion } from 'framer-motion';
import { NavLink } from 'react-router-dom';
import Buttons from '../components/Buttons';
import SwipeablePage from '../components/SwipeablePage';

const Project = () => {

  const { currentPage, setCurrentPage, handleSwipe } = SwipeablePage();

  useEffect(() => {
    document.body.style.backgroundColor = '#04081d';
    document.querySelectorAll('.nav-background, .nav-projects, .project-main').forEach((element) => {
      element.style.backgroundColor = '#ff3d00';
      element.style.boxShadow = '0 0 20px rgba(255, 61, 0, 0.5)';
    });
    const blobs = document.querySelectorAll('.blob');
    blobs.forEach((blob) => {
      const classNames = Array.from(blob.classList);
      classNames.forEach((className) => {
        if (className !== 'blob') {
          blob.classList.remove(className);
        }
      });
      blob.classList.add('blob-orange');
    });
  }, []);

  return (
    <section {...handleSwipe} className='project-page'>
      <div className='project-container'>
        {projectsData.map((project, index) => (
        <NavLink to={project.navlink}className='redirect'>
          <motion.div className='project-card' key={project.title} initial={{ opacity: 0, transform: `translateX(${index % 2 === 1 ? 1500 : -1500}px)`}} animate={{ opacity: 1, transform: 'translateX(0px)' }} transition={{ duration: 0.4, delay: index * 0.4, ease: 'easeOut' }} >         
              <h2>{project.title}</h2>
              <picture>
                <source srcSet={project.small_img} media='(max-width: 768px)'/>
                <source srcSet={project.img} media='(min-width: 769px)' />
                <img src={project.img} alt={`photo de ${project.title}`} />
              </picture>
              <div className="sticker"></div>
              <div className='circle'></div>
          </motion.div>
          </NavLink>
        ))}
      </div>
      <Buttons left={'/'} right={'/contact'} down={'/projet-1'} linkDown={'Projet 1'} linkRight={'Contact'} linkLeft={'Accueil'} />
    </section>
  );
};

export default Project;