import React, { useState } from 'react';
import { useSwipeable } from 'react-swipeable';

const SwipeablePage = () => {
    const [currentPage, setCurrentPage] = useState('/'); // 
  
    const handleSwipe = useSwipeable({
          onSwipedLeft: () => {
          if (currentPage === '/') {
            setCurrentPage('/projets');
          } else if (currentPage === '/projets') {
            setCurrentPage('/contact');
          }
        },
          onSwipedRight: () => {
          if (currentPage === '/contact') {
            setCurrentPage('/projets');
          } else if (currentPage === '/projets') {
            setCurrentPage('/');
          }
        },
          onSwipedUp: () => {
          if (currentPage === '/projets') {
            setCurrentPage('/projet-1');
          } else if (currentPage === '/projets') {
            setCurrentPage('/');
          } else if (currentPage === '/projet-1') {
            setCurrentPage('/projet-2')
          } else if (currentPage === '/projet-2') {
            setCurrentPage('/projet-3')
          } else if (currentPage === '/projet-3') {
            setCurrentPage('/projet-4')
          }
        },
          onSwipedDown: () => {
            if (currentPage === '/projet-1') {
              setCurrentPage('/projets');
            } else if (currentPage === '/projet-2') {
              setCurrentPage('/projet-1')
            } else if (currentPage === '/projet-3') {
              setCurrentPage('/projet-2')
            } else if (currentPage === '/projet-4') {
              setCurrentPage('/projet-3')
            }
        }
      });

      return  { currentPage, setCurrentPage, handleSwipe };
  };
  
  export default SwipeablePage;