import React from 'react';
import Buttons from '../components/Buttons';
import ProjectComponent from '../components/ProjectComponent';
import SwipeablePage from '../components/SwipeablePage';

const Project4 = () => {

    const { currentPage, setCurrentPage, handleSwipe } = SwipeablePage();
    
    const backColor = '#02577a';
    const textColor = 'beige';
    const shadowColor = '0 0 20px blue';
    const blobColor = 'blob-beige';

    return (
        <div {...handleSwipe} className="project">
            <ProjectComponent projectNumber={3}
              backColor={backColor}
              textColor={textColor}
              shadowColor={shadowColor}
              blobColor={blobColor} />
            <Buttons up={"/projet-3"} linkUp={"Projet 3"} />
        </div>
    );
};

export default Project4;