import React, { useEffect } from 'react';
import Buttons from '../components/Buttons';
import Form from '../components/Form';
import SwipeablePage from '../components/SwipeablePage';

const Contact = () => {

  const { currentPage, setCurrentPage, handleSwipe } = SwipeablePage();

    useEffect(() => {
        document.body.style.backgroundColor = '#04081d';
        document.querySelectorAll('.nav-background, .nav-projects, .project-main').forEach((element) => {
            element.style.backgroundColor = '#ff3d00';
            element.style.boxShadow = '0 0 20px rgba(255, 61, 0, 0.5)';
          });
        const blobs = document.querySelectorAll('.blob');
        blobs.forEach((blob) => {
          const classNames = Array.from(blob.classList);
          classNames.forEach((className) => {
            if (className !== 'blob') {
              blob.classList.remove(className);
            }
          });
          blob.classList.add('blob-orange');
        });
      }, []);

    return (
        <div {...handleSwipe}>
            <Form/>
            <Buttons left={"/projets"} linkLeft={'Projets'} />
        </div>
    );
};

export default Contact;