import React from 'react';
import Buttons from '../components/Buttons';
import ProjectComponent from '../components/ProjectComponent';
import SwipeablePage from '../components/SwipeablePage';

const Project2 = () => {

    const { currentPage, setCurrentPage, handleSwipe } = SwipeablePage();

    const backColor = '#0c6d63';
    const textColor = '#baffc9';
    const shadowColor = '0 0 20px greenyellow';
    const blobColor = 'blob-green';

    return (
        <div {...handleSwipe} className="project">
            <ProjectComponent projectNumber={1}
            backColor={backColor}
              textColor={textColor}
              shadowColor={shadowColor}
              blobColor={blobColor} />
            <Buttons up={"/projet-1"} down={"/projet-3"} linkUp={"Projet 1"} linkDown={'Projet 3'} />
        </div>
    );
};

export default Project2;