import React, { useEffect, useState } from 'react';
import { projectsData } from '../data/projectsData';
import { motion, AnimatePresence } from 'framer-motion';
import Modal from 'react-modal';

const ProjectComponent = ({ projectNumber, backColor, textColor, shadowColor, blobColor }) => {
  const [currentProjectID, setCurrentProjectID] = useState(projectNumber);

  useEffect(() => {

    document.body.style.backgroundColor = backColor;
    document.querySelectorAll('.button-container>a, .languages-li').forEach((element) => {
      element.style.backgroundColor = backColor;
    });
    document.querySelectorAll('.change-page>a').forEach((element) => {
      element.style.color = textColor;
    });
    document.querySelectorAll('.nav-background, .nav-projects, .project-main').forEach((element) => {
      element.style.backgroundColor = textColor;
      element.style.boxShadow = shadowColor;
    });
    const blobs = document.querySelectorAll('.blob');
    blobs.forEach((blob) => {
      const classNames = Array.from(blob.classList);
      classNames.forEach((className) => {
        if (className !== 'blob') {
          blob.classList.remove(className);
        }
      });
      blob.classList.add(blobColor);
    });
  }, []);

  useEffect(() => {
    setCurrentProjectID(projectNumber);
  }, [projectNumber]);

  const currentProject = projectsData[currentProjectID];

  const isMobile = window.innerWidth <= 768;
  const [isModalOpen, setIsModalOpen] = useState(false);

  const openModal = () => {
    setIsModalOpen(true);
  };
  const closeModal = () => {
    setIsModalOpen(false);
  };
  
  const variants = {
    hidden: {
      opacity: 0,
      transform: 'translateY(100%)',
    },
    visible: {
      opacity: 1,
      transform: 'translateY(0)',
      transition: {
        duration: 0.5,
      },
    },
    exit: {
      opacity: 0,
      transform: 'translateY(-100%)',
      transition: {
        duration: 0.35,
      },
    },
  };

  const titleVariants = {
    hidden: {
      opacity: 0,
      x: -200,
    },
    visible: {
      opacity: 1,
      x: 0,
      transition: {
        delay: 0.6,
        duration: 1.2,
      },
    },
  };

  const infosVariants = {
    hidden: {
      opacity: 0,
      x: -100,
    },
    visible: {
      opacity: 1,
      x: 0,
      transition: {
        duration: 1.6,
        delay: 0.2,
      },
    },
  };

  const imgAnim = {
    hidden: {
      opacity: 0,
      x: Math.round(Math.random() * 200) - 100,
      y: Math.round(Math.random() * 200) - 100,
    },
    visible: {
      opacity: 1,
      x: 0,
      y:0,
      transition: {
        duration: 1.6,
        delay: 0.4,
      },
    },
  };

  return (
    <motion.div className="project-main" initial="hidden" animate={'visible'} exit="exit" variants={variants} >
      <div className="project-content">
            <AnimatePresence>
            <motion.h2 key={currentProject.title} variants={titleVariants} initial="hidden" animate="visible" exit="hidden">
                {currentProject.title}
            </motion.h2> 
            </AnimatePresence>
            <motion.div className='project-without-title' key={currentProject.title} variants={infosVariants} initial="hidden" animate="visible">
                <p className="date" >{currentProject.date}</p>
                <div className='line'></div>
                <ul className="languages-ul">
                    {currentProject.languages.map((item) => (
                        <li className="languages-li" key={item}>{item}</li>
                    ))}
                </ul>
                <div className='line'></div>
                <span>
                    <p>{currentProject.infos}</p>
                </span>
                <div className='line'></div>
            </motion.div>
            <div className="button-container">
                {currentProject.link ? (
                    <a href={currentProject.link} target="_blank" rel="noopener noreferrer" className="hover">
                    <span className="button">Voir le site</span>
                    </a>
                ) : (
                    <a href={currentProject.git} target="_blank" rel="noopener noreferrer" className="hover">
                    <span className="button">Voir le GitHub</span>
                    </a>
                )}
            </div>
      </div>
      <motion.div className="img-content">
        <div className="img-container" onClick={openModal}>
          <motion.img key={currentProject.img} src={currentProject.img} alt={currentProject.title} className="img" variants={imgAnim} initial="hidden" animate="visible" />
        </div>
      </motion.div>
      {!isMobile && (
            <Modal isOpen={isModalOpen} onRequestClose={closeModal} contentLabel="image agrandrie">
                <div className="modal-content">
                    <button className="close-button" onClick={closeModal}>Fermer</button>
                    <img src={currentProject.img} alt={currentProject.title} className="enlarged-img" />
                </div>
            </Modal>
      )}
    </motion.div>
  );
};

export default ProjectComponent;