import React from 'react';
import { NavLink } from 'react-router-dom';
import { motion } from 'framer-motion';

const Nav = () => {
  return (
    <motion.nav id='nav' initial={{opacity:0}} animate={{opacity:1}} transition={{duration:5}}>
      <div className="nav-background"></div>
      <ul>
        <NavLink to="/" className={(nav) => (nav.isActive ? "nav-active hover" : "hover")} key="accueil">
          <li>Accueil</li>
        </NavLink>
        <li className="nav-portfolio">
          <NavLink to="/projets" className={(nav) => (nav.isActive ? "nav-active hover" : "hover")} key="portfolio">
            Portfolio
          </NavLink>
          <ul className="nav-projects">
            <li key="projet-1">
              <NavLink to="/projet-1" className={(nav) => (nav.isActive ? "nav-active hover" : "hover")}>
                Projet 1
              </NavLink>
            </li>
            <li key="projet-2">
              <NavLink to="/projet-2" className={(nav) => (nav.isActive ? "nav-active hover" : "hover")}>
                Projet 2
              </NavLink>
            </li>
            <li key="projet-3">
              <NavLink to="/projet-3" className={(nav) => (nav.isActive ? "nav-active hover" : "hover")}>
                Projet 3
              </NavLink>
            </li>
            <li key="projet-4">
              <NavLink to="/projet-4" className={(nav) => (nav.isActive ? "nav-active hover" : "hover")}>
                Projet 4
              </NavLink>
            </li>
          </ul>
        </li>
        <NavLink to="/contact" className={(nav) => (nav.isActive ? "nav-active hover" : "hover")} key="contact">
          <li>Contact</li>
        </NavLink>
      </ul>
    </motion.nav>
  );
};

export default Nav;