import React, { useEffect } from 'react';

const DynamicText = () => {

    useEffect(() => {
        const firstNameTarget = document.getElementById('firstName-target');
        const lastNameTarget = document.getElementById('lastName-target');
        let firstName = "Ludovic";
        let lastName = " Sagnal";
        let letterIndex = 0;
        let letterIndexBis = lastName.length - 1;

        const createLetter = () => {
            const letter = document.createElement('span');
            firstNameTarget.appendChild(letter);
            letter.classList.add('letter');
            letter.style.animation = "anim 2s ease forwards";
            letter.textContent = firstName[letterIndex];
        };
        const createLetterBis = () => {
            const letterBis = document.createElement('span');
            lastNameTarget.prepend(letterBis);
            letterBis.classList.add('letter-bis');
            letterBis.style.animation = "animBis 2s ease forwards";
            letterBis.textContent = lastName[letterIndexBis];
        };

        const loop = () => {
            if (letterIndex < firstName.length || letterIndexBis >= 0) {
                setTimeout (() => {
                    if (letterIndex < firstName.length) {
                        createLetter();
                        letterIndex++;
                    }
                    if (letterIndexBis >= 0) {
                        createLetterBis();
                        letterIndexBis--;
                    }
                    loop();
                }, 500);
            }
        }; 
        loop();
    },[]);

    return (
        <h1 className="dynamic-text">
            <span id="firstName-target"></span>
            <span id="lastName-target"></span>
        </h1>
    );
};

export default DynamicText;