import React, { useEffect, useState } from 'react';
import Buttons from '../components/Buttons';
import { motion } from 'framer-motion';
import DynamicText from '../components/DynamicText';
import { skillsData } from '../data/skillsData';
import SwipeablePage from '../components/SwipeablePage';

const Home = () => {

  const { currentPage, setCurrentPage, handleSwipe } = SwipeablePage();

  const [animationProps, setAnimationProps] = useState({
    x: [0, 10, -10, 0],
    y: [0, 10, 10, 0],
    scale: [0, 1, 1.1, 0.9, 1],
    height: [400, 420, 450],
    width: [400, 380, 450],
  });

  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth < 768) {
        setAnimationProps({
          x: [0, 5, -5, 0],
          y: [0, 5, 5, 0],
          scale: [0, 1, 1.05, 0.95, 1],
          height: [200, 210, 225],
          width: [200, 190, 225],
        });
      } else {
        setAnimationProps({
          x: [0, 10, -10, 0],
          y: [0, 10, 10, 0],
          scale: [0, 1, 1.1, 0.9, 1],
          height: [300, 320, 350],
          width: [300, 280, 350],
        });
      }
    };

    handleResize();
    window.addEventListener('resize', handleResize);

    return () => window.removeEventListener('resize', handleResize);
  }, []);

    useEffect(() => {
        document.body.style.backgroundColor = '#04081d';
        document.querySelectorAll('.nav-background, .nav-projects, .project-main').forEach((element) => {
            element.style.backgroundColor = '#ff3d00';
            element.style.boxShadow = '0 0 20px rgba(255, 61, 0, 0.5)';
          });
        const blobs = document.querySelectorAll('.blob');
        blobs.forEach((blob) => {
          const classNames = Array.from(blob.classList);
          classNames.forEach((className) => {
            if (className !== 'blob') {
              blob.classList.remove(className);
            }
          });
          blob.classList.add('blob-orange');
        });
      }, []);

      return (
          <main {...handleSwipe}>
            <span id='top'></span>
            <DynamicText />
            <motion.h2 initial={{ opacity: 0 }} animate={{ opacity: 1 }} transition={{ duration: 3, delay: 4.8 }}>Développeur Full Stack</motion.h2>
            <div className="blob-photo-container">
              <motion.div
                className='blob-photo' animate={{ ...animationProps }} transition={{ duration: 30, repeat: Infinity, repeatType: 'reverse' }} >
                <img src='./img/NagnalSnack1.webp' alt='' />
              </motion.div>
            </div>
              <motion.h2 initial={{opacity:0}} animate={{opacity:1}} transition={{duration: 1.5, delay: 5}} >Compétences</motion.h2>
              <motion.ul className="skills-container" initial={{opacity:0}} animate={{opacity:1}} transition={{duration: 1.5, delay: 5}}>
                <li className="front-container">
                  {skillsData
                    .filter((skill) => skill.type === 'front')
                    .map((skill, index) => (
                      <motion.div className='blob-skills' key={index} initial={{opacity:0}} animate={{opacity:1}} transition={{ duration: .1, delay: index * .3 + 6.5 }}>
                          <motion.img src={`./img/skills/${skill.img}`} alt="" whileHover={{y:5}} />
                          <p className='skills-label'>{skill.name}</p>
                      </motion.div>
                    ))}
                </li>
                <span className="line"></span>
                <li className="back-container">
                  {skillsData
                    .filter((skill) => skill.type === 'back')
                    .map((skill, index) => (
                      <motion.div className='blob-skills' key={index} initial={{opacity:0}} animate={{opacity:1}} transition={{ duration: .1, delay: index * .2 + 8.5 }}>
                          <motion.img src={`./img/skills/${skill.img}`} alt="" whileHover={{y:5}} />
                          <p className='skills-label'>{skill.name}</p>
                      </motion.div>
                    ))}
                </li>
                <span className="line"></span>
                <li className="other-container">
                  {skillsData
                    .filter((skill) => skill.type !== 'front' && skill.type !== 'back')
                    .map((skill, index) => (
                      <motion.div className='blob-skills' key={index} initial={{opacity:0}} animate={{opacity:1}} transition={{ duration: .1, delay: index * 0.1 + 9 }}>
                          <motion.img src={`./img/skills/${skill.img}`} alt="" whileHover={{y:5}} />
                          <p className='skills-label'>{skill.name}</p>
                      </motion.div>
                    ))}
                </li>
              </motion.ul>
            <Buttons right={"/projets"} linkRight={'Projets'} />
          </main>
      );      
};

export default Home;