import React from 'react';
import { NavLink } from 'react-router-dom';

const Buttons = (props) => {

  return (
    <div className="change-page">
      {props.left && (
        <NavLink to={props.left} className={'arrow left hover'} >
          <span>&#9667;</span>
          <p>{props.linkLeft}</p>
        </NavLink>
      )}
      {props.right && (
        <NavLink to={props.right} className={'arrow right hover'}>
          <span>&#9657;</span>
          <p>{props.linkRight}</p>
        </NavLink>
      )}
      {props.down && (
        <NavLink to={props.down} className={'arrow down hover'}>
          <p>{props.linkDown}</p>
          <span>&#9663;</span>
        </NavLink>
      )}
      {props.up && (
        <NavLink to={props.up} className={'arrow up hover'}>
          <span>&#9653;</span>
          <p>{props.linkUp}</p>
        </NavLink>
      )}
    </div>
  );
};

export default Buttons;