import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';

const LavaLamp = () => {

  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  const handleResize = () => {
    setWindowWidth(window.innerWidth);
  };

  useEffect(() => {
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const blobs = [
    { top: '50%', left: '80%'},
    { top: '20%', left: '10%'},
    { top: '10%', left: '90%'},
    { top: '60%', left: '20%'},
    { top: '35%', left: '15%'},
    { top: '70%', left: '90%'},
    { top: '80%', left: '5%'},
    { top: '70%', left: '90%'},
    { top: '70%', left: '40%'},
    { top: '80%', left: '60%'},
    { top: '10%', left: '35%'},
    { top: '25%', left: '65%'},
    { top: '40%', left: '35%'},
    { top: '60%', left: '65%'},
  ];

  const getVisibleBlobs = () => {
    if (windowWidth < 768) {
      return blobs.filter((blob) => blob.top !== '10%' && blob.left !== '10%');
    } else {
      return blobs;
    }
  };

  const getRandomDelay = () => Math.random() * 3;

  const getRandomAnimation = () => ({
    x: [getRandomValueInRange(-100, 100), getRandomValueInRange(-100, 100), getRandomValueInRange(-100, 100), getRandomValueInRange(-100, 100)],
    y: [getRandomValueInRange(-100, 100), getRandomValueInRange(-100, 100), getRandomValueInRange(-100, 100), getRandomValueInRange(-100, 100)],
    height:  getRandomValueInRange(100, 160),
    width:  getRandomValueInRange(100, 160),
    scale: [1, 1.8, 0.8, 1],
    rotate: Math.random() * 360,
  });
  
  const getRandomValueInRange = (min, max) => {
    return Math.random() * (max - min) + min;
  };

  const getRandomVariant = () => ({
    animate: getRandomAnimation(),
    transition: {
      duration: getRandomValueInRange(10, 20),
      repeat: Infinity,
      repeatType: 'reverse',
      delay: getRandomDelay(),
    },
  });

  return (
    <div className='lava-lamp'>
      {getVisibleBlobs().map((blob, index) => (
        <motion.div key={index} className='blob blob-orange'
          style={{
            top: blob.top,
            left: blob.left,
            width: blob.width,
            height: blob.height,
          }}
          animate={getRandomVariant().animate}
          transition={getRandomVariant().transition}
        />
      ))}
    </div>
  );
};

export default LavaLamp;