import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';

const NavSmart = () => {
  const [isOpen, setIsOpen] = useState(false);

  const handleNav = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div className="smart-nav" onClick={handleNav}>
      <img src="./img/up-burger.webp" alt="" className={isOpen ? 'rotate-down' : ''} />
      <img src="./img/mid-burger.webp" alt="" className={isOpen ? 'hide' : ''} />
      <img src="./img/bottom-burger.webp" alt="" className={isOpen ? 'rotate-up' : ''} />
      <ul className={isOpen ? 'open-nav' : 'hide'}>
        <NavLink to="/">
          <li>Accueil</li>
        </NavLink>
        <NavLink to="/projets">
          <li>Portfolio</li>
        </NavLink>
        <NavLink to="/contact">
          <li>Contact</li>
        </NavLink>
      </ul>
    </div>
  );
};

export default NavSmart;
