import React from 'react';
import Buttons from '../components/Buttons';
import ProjectComponent from '../components/ProjectComponent';
import SwipeablePage from '../components/SwipeablePage';

const Project1 = () => {

  const { currentPage, setCurrentPage, handleSwipe } = SwipeablePage();

  const backColor = '#89d6fb';
  const textColor = '#546fe4';
  const shadowColor = '0 0 20px rgb(217, 66, 217)';
  const blobColor = 'blob-blue';

    return (
        <div {...handleSwipe} className="project"> <ProjectComponent projectNumber={0} 
              backColor={backColor}
              textColor={textColor}
              shadowColor={shadowColor}
              blobColor={blobColor} />
            <Buttons up={"/projets"} down={"/projet-2"} linkUp={"Projets"} linkDown={'Projet 2'} />
        </div>
    );
};

export default Project1;